.about {
  background-color: white;
  display: flex;
  width: 100%;
  overflow: hidden;
}
    h3 {
      padding-top: 30px;
    }
    h3, h4 {
      margin: 12px;
      text-align: justify;
      line-height: 20px;

    }
    h4 {
      font-weight: normal;
    }
    .content--about {
      margin: 2rem;
      flex: 1 1;
    }
    h1 {
        padding: 0 0 10px 20px;
        font-size: 3em;
        font-weight: normal;
        align-self: center;
        margin-top: 100px;
        color: #444649;
        text-decoration: underline;

      }
    p {
        padding: 0 20px 0 20px;
        text-align: justify;
        line-height: 26px;
        margin-right: 10px;
       
      }
    .content--about--right {
        background-color: rgb(87, 87, 87);
        flex: 1.4 1;
        
      }
    .house--pic {
        margin-right: 50px;
        width: 100%;
        height: 100%;
      }
   
